"use client"

import Link from "next/link"
import { usePathname } from "next/navigation"
import { ArrowUp, Linkedin, Github, Youtube } from "lucide-react"
import { useState, useEffect } from "react"

export function Footer() {
  const pathname = usePathname()
  const [showScrollTop, setShowScrollTop] = useState(false)

  useEffect(() => {
    const handleScroll = () => {
      setShowScrollTop(window.scrollY > 300)
    }

    window.addEventListener("scroll", handleScroll)
    return () => window.removeEventListener("scroll", handleScroll)
  }, [])

  const scrollToTop = () => {
    window.scrollTo({ top: 0, behavior: "smooth" })
  }

  const isActive = (path: string) => pathname === path

  return (
    <footer className="bg-muted border-t border-border">
      <div className="container mx-auto px-6 py-12">
        <div className="flex flex-col md:flex-row items-center justify-between gap-8">
          <div className="flex flex-col items-center md:items-start gap-4">
            <Link href="/" className="text-xl font-bold text-foreground hover:text-accent transition-colors">
              {"<dev/>"}
            </Link>
            <p className="text-sm text-muted-foreground">Building exceptional web experiences</p>
          </div>

          <nav className="flex flex-wrap items-center justify-center gap-6">
            <Link
              href="/about"
              className={`text-sm font-medium transition-colors ${
                isActive("/about") ? "text-foreground" : "text-muted-foreground hover:text-foreground"
              }`}
            >
              about me
            </Link>
            <Link
              href="/experience"
              className={`text-sm font-medium transition-colors ${
                isActive("/experience") ? "text-foreground" : "text-muted-foreground hover:text-foreground"
              }`}
            >
              experience
            </Link>
            <Link
              href="/portfolio"
              className={`text-sm font-medium transition-colors ${
                isActive("/portfolio") ? "text-foreground" : "text-muted-foreground hover:text-foreground"
              }`}
            >
              portfolio
            </Link>
            <Link
              href="/contact"
              className={`text-sm font-medium transition-colors ${
                isActive("/contact") ? "text-foreground" : "text-muted-foreground hover:text-foreground"
              }`}
            >
              contact
            </Link>
          </nav>

          <div className="flex items-center gap-4">
            <a
              href="https://linkedin.com"
              target="_blank"
              rel="noopener noreferrer"
              className="text-muted-foreground hover:text-foreground transition-colors"
              aria-label="LinkedIn"
            >
              <Linkedin className="w-5 h-5" />
            </a>
            <a
              href="https://github.com"
              target="_blank"
              rel="noopener noreferrer"
              className="text-muted-foreground hover:text-foreground transition-colors"
              aria-label="GitHub"
            >
              <Github className="w-5 h-5" />
            </a>
            <a
              href="https://youtube.com"
              target="_blank"
              rel="noopener noreferrer"
              className="text-muted-foreground hover:text-foreground transition-colors"
              aria-label="YouTube"
            >
              <Youtube className="w-5 h-5" />
            </a>
          </div>
        </div>

        <div className="mt-8 pt-8 border-t border-border text-center text-sm text-muted-foreground">
          <p>Â© {new Date().getFullYear()} All rights reserved.</p>
        </div>
      </div>

      {/* Scroll to top button */}
      <button
        onClick={scrollToTop}
        className={`fixed bottom-8 right-8 bg-accent text-accent-foreground rounded-full p-3 shadow-lg transition-all duration-300 hover:scale-110 hover:shadow-xl ${
          showScrollTop ? "opacity-100 translate-y-0" : "opacity-0 translate-y-16 pointer-events-none"
        }`}
        aria-label="Scroll to top"
      >
        <ArrowUp className="w-5 h-5" />
      </button>
    </footer>
  )
}
